<template>
  <div class="flex-col">
    <div class="flex justify-center">
        <bar-loader :loading="isLoading" :color="'#88caca'" :size="200"/>
        <px-assets-table v-if="!isLoading" :assets="assets" />
    </div>
    <template v-if="asset.id && !isLoading">
      <div class="flex flex-col sm:flex-row justify-around items-center">
        <div class="flex flex-col items-center">
          <img
            :src="
              `https://static.coincap.io/assets/icons/${asset.symbol.toLowerCase()}@2x.png`
            "
            :alt="asset.name"
            class="w-20 h-20 mr-5"
          />
          <h1 class="text-5xl">
            {{ asset.name }}
            <small class="sm:mr-2 text-gray-500">{{ asset.symbol }}</small>
          </h1>
        </div>

        <div class="my-10 flex flex-col">
          <ul>
            <li class="flex justify-between">
              <b class="text-gray-600 mr-10 uppercase">Ranking</b>
              <span>#{{ asset.rank }}</span>
            </li>
            <li class="flex justify-between">
              <b class="text-gray-600 mr-10 uppercase">Precio actual</b>
              <span>{{ asset.priceUsd | dollar }}</span>
            </li>
            <li class="flex justify-between">
              <b class="text-gray-600 mr-10 uppercase">Precio más bajo</b>
              <span>{{ min | dollar }}</span>
            </li>
            <li class="flex justify-between">
              <b class="text-gray-600 mr-10 uppercase">Precio más alto</b>
              <span>{{ max | dollar }}</span>
            </li>
            <li class="flex justify-between">
              <b class="text-gray-600 mr-10 uppercase">Precio Promedio</b>
              <span>{{ avg | dollar }}</span>
            </li>
            <li class="flex justify-between">
              <b class="text-gray-600 mr-10 uppercase">Variación 24hs</b>
              <span>{{ asset.changePercent24Hr | dollarPercent }}</span>
            </li>
          </ul>
        </div>
      </div>
      <div class="my-10" id="chart-coin">
          <column-chart :colors="['#0b0', '#666']" 
            width="800px" height="500px" label="Value"
            :id="'chart-coin'"
            :curve="false"
            :min="min"
            :max="max"
            :data="this.history.map(h => [h.date, parseFloat(parseFloat(h.priceUsd).toFixed(2))])">
          </column-chart>
        </div>
    </template>
  </div>


</template>

<script>
import api from '@/api'

export default {
  name: 'CoinDetail',

  data() {
    return {
      asset: {},
      history: [],
      isLoading : true
    }
  },

  computed: {
    min() {
      return Math.min(
        ...this.history.map(h => parseFloat(h.priceUsd).toFixed(2))
      )
    },

    max() {
      return Math.max(
        ...this.history.map(h => parseFloat(h.priceUsd).toFixed(2))
      )
    },

    avg() {
      return Math.abs(
        ...this.history.map(h => parseFloat(h.priceUsd).toFixed(2))
      )
    }
  },

  created() {
    this.getCoin()
  },

  methods: {
    getCoin() {
      const id = this.$route.params.id
      this.isLoading = true
      
      Promise.all([api.getAsset(id), api.getAssetHistory(id)]).then(
        ([asset, history]) => {
          this.asset = asset
          this.history = history
        })
        .finally(() => (this.isLoading = false))
    }
  },

  mounted(){
        setInterval(()=>{
            this.getCoin()
        },
        5000);
    },
}

</script>

